  font-weight: 300;
}
</style>

<script>
import Navbar from "./components/Navbar";

export default {
  name: "App",
  data: function() {
    return {
      jwt: !!localStorage.jwt,
    };
  },
  components: {
    Navbar,
  },
  watch: {
    $route: {
      immediate: true,
