<template>
  <nav>
    <div class="area">
      <router-link class="brand" to="/">
        <img src="../assets/logo.png" />
        <h3>Umbrella</h3>
      </router-link>
      <div class="list" id="nav-list">
        <router-link class="item" to="/dialogs">Dialog</router-link>
        <router-link class="item" to="/profile">Profile</router-link>
        <router-link class="logout" to="/logout">Logout</router-link>
      </div>

      <button class="toggle" id="toggle" v-on:click="openToggle">
        <span class="icon"></span>
      </button>
    </div>
  </nav>
</template>

<style lang="scss" scoped>
nav {
  border-bottom: 1px solid lightgray;

  a {
    text-decoration: none;
    color: black;
  }

  .area {
    display: flex;
    margin-right: auto;
    margin-left: auto;
    align-items: stretch;
    justify-content: space-between;

    @media (min-width: 768px) {
      width: 750px;
    }
    @media (min-width: 992px) {
      width: 970px;
    }
    @media (min-width: 1200px) {
      width: 1140px;
    }

    .brand {
      display: flex;
      align-items: center;
      padding: 10px;

      img {
        widows: 40px;
        height: 40px;
        margin-right: 10px;
      }
    }

    .logout {
      display: flex;
      align-items: center;
      padding: 10px;
      color: darkred;
      margin-left: 20px;

      &:hover {
        color: rgb(165, 6, 6);
        text-decoration: underline;
      }
    }

    .list {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      white-space: nowrap;

      .item {
        display: block;
        flex-shrink: 0;
        padding: 21px 10px;
        border-left: 1px solid transparent;
        border-right: 1px solid transparent;

        transition: all 300ms;
        text-transform: uppercase;

        &:hover {
          position: relative;
          color: darkred;
          background: rgb(235, 235, 235);
          border-left: 1px solid lightgray;
          border-right: 1px solid lightgray;
        }
      }

      .router-link-active {
        color: darkred;
        background: rgb(248, 248, 248);
        border-left: 1px solid lightgray;
        border-right: 1px solid lightgray;
      }

      display: none;

      @media (min-width: 768px) {
        display: flex;
      }

      @media (max-width: 768px) {
        position: fixed;
        top: 60px;
        left: 0;
        width: 100%;
        overflow-y: hidden;
        overflow-x: auto;
        border-top: 1px solid lightgray;
        border-bottom: 1px solid lightgray;
        background: white;
      }

      &.-on {
        display: flex;
      }
    }

    .toggle {
      border: 0;
      background-color: transparent;
      display: inline-block;
      outline: none;
      border: 0;
      background-color: transparent;
      background-image: none;
      vertical-align: middle;
      text-align: center;
      white-space: nowrap;
      cursor: pointer;
      touch-action: manipulation;
      user-select: none;

      // Напомню, что 20 — это значение line-height у body
      padding: 10px;

      .icon {
        position: relative;
        margin-top: 8px;
        margin-bottom: 8px;

        &,
        &:before,
        &:after {
          display: block;
          width: 24px;
          height: 3px;
          transition: background-color 0.3s linear, transform 0.3s linear;
          background-color: #555555;
        }

        &:before,
        &:after {
          position: absolute;
          content: "";
        }
        &:before {
          top: -8px;
        }
        &:after {
          top: 8px;
        }
      }

      &.-active > .icon {
        background-color: transparent;
        &:before {
          transform: translateY(8px) rotate(45deg);
        }
        &:after {
          transform: translateY(-8px) rotate(-45deg);
        }
      }

      @media (min-width: 768px) {
        display: none;
      }
    }
  }
}
</style>

<script>
export default {
  methods: {
    openToggle: function() {
      document.getElementById("toggle").classList.toggle("-active");
      document.getElementById("nav-list").classList.toggle("-on");
    },
  },
};
</script>
